function Footer({ func, name }) {
  return (
    <div className="w-full flex h-16 bg-black flex-row-reverse">
      <button
        className="bg-fwd rounded text-white w-100 px-4 my-2 mx-2 hover:opacity-90"
        onClick={func}
      >
        {name}
      </button>
    </div>
  );
}

export default Footer;
